.main {
    padding: 105px 0 70px 0;

    @media (max-width: 1024px) {
        padding: 65px 0 60px 0;
    }

    @media (max-width: 768px) {
        padding: 40px 0 60px 0;
    }

    @media (max-width: 600px) {
        overflow: hidden;
    }
}

.main_wrapper {
    display: flex;
    justify-content: space-between;

    position: relative;

    @media (max-width: 768px) {
        display: block;
    }
}
